import React, { createContext, useContext, useState, useRef } from 'react';

const MyContext = createContext();

export const MyProvider = ({ children }) => {
  // const [client, setClient] = useState(null);
  //  
    const [timeList, setTimeList] = useState([]); //  
    const [tempList, setTempList] = useState([]); //   
    const [humidityList, setHumidityList] = useState([]); //  
    const [weatherList, setWeatherList] = useState([]); //   
    const [windList, setWindList] = useState([]); //  
    const [rainfallList, setRainfallList] = useState([]); //     tSVL1, tSVL2, tSP1, tSP2, oT1, oGS1
    const [ultraDustList, setUltraDustList] = useState([]); //  
    const [dustList, setDustList] = useState([]); //  
  //   
    const [iT1List, setIT1List] = useState([]); //   
    const [iGS1List, setIGS1List] = useState([]); //    
  //  
    const [tSL1List, setTSL1List] = useState([]); //  
    const [tSL2List, setTSL2List] = useState([]); // 2 
    const [tST1List, setTST1List] = useState([]); //  
    const [tST2List, setTST2List] = useState([]); // 2 
    const [tST3List, setTST3List] = useState([]); // 3 
    const [tSVL1List, setTSVL1List] = useState([]); //  
    const [tSVL2List, setTSVL2List] = useState([]); // 2 
    const [tSP1List, setTSP1List] = useState([]); //  
    const [tSP2List, setTSP2List] = useState([]); // 2 
  //   
    const [oT1List, setOT1List] = useState([]); //   
    const [oGS1List, setOGS1List] = useState([]); //    

    const currentTemp = useRef(0); //   
    const currentHumidity  = useRef(0); //  
    const currentWeather = useRef(0); //   
    const currentWind  = useRef(0); //  
    const currentRainfall  = useRef(0); //  
    const currentUltraDust  = useRef(0); //  
    const currentDust = useRef(0); //  
    const currentIT1 = useRef(0); //   
    const currentIGS1 = useRef(0); //    
    const currentTSL1 = useRef(0); //  
    const currentTSL2 = useRef(0); //  2
    const currentTST1 = useRef(0); //  
    const currentTST2 = useRef(0); //  2
    const currentTST3 = useRef(0); //  3
    const currentTSVL1 = useRef(0); //  
    const currentTSVL2 = useRef(0); //  2
    const currentTSP1 = useRef(0); //  
    const currentTSP2  = useRef(0); //  2
    const currentOT1  = useRef(0); //   
    const currentOGS1  = useRef(0); //    

    const iV1 = useRef(null);
    const iM1 = useRef(null);
    const iSV1 = useRef(null);
    const iMV1 = useRef(null);
    const tSSV1 = useRef(null);
    const tSSV2 = useRef(null);
    const tSMV1 = useRef(null);
    const tSMV2 = useRef(null);
    const oV1 = useRef(null);
    const oM1 = useRef(null);
    const oSV1 = useRef(null);
    const oMV1 = useRef(null);
    // iV1 iM1 iSV1 iMV1 tSSV1 tSSV2 tSMV1 tSMV2 oV1 oM1 oSV1 oMV1
    const stompClientRef = useRef(null);

  const contextValue = {
    stompClientRef,
    timeList, setTimeList, tempList, setTempList, humidityList, setHumidityList, weatherList, setWeatherList, windList, 
    setWindList, rainfallList, setRainfallList, ultraDustList, setUltraDustList, dustList, setDustList, iT1List, setIT1List,
    iGS1List, setIGS1List, tSL1List, setTSL1List, tSL2List, setTSL2List, tST1List, setTST1List, tST2List, setTST2List,
    tST3List, setTST3List, tSVL1List, setTSVL1List, tSVL2List, setTSVL2List, tSP1List, setTSP1List, tSP2List, setTSP2List,
    oT1List, setOT1List, oGS1List, setOGS1List,

    currentTemp, currentHumidity, currentWeather, currentWind, currentRainfall, currentUltraDust, currentDust, currentIT1, 
    currentIGS1, currentTSL1, currentTSL2, currentTST1, currentTST2, currentTST3, currentTSVL1, currentTSVL2, currentTSP1,
    currentTSP2, currentOT1, currentOGS1,
    iV1, iM1, iSV1, iMV1, tSSV1, tSSV2, tSMV1, tSMV2, oV1, oM1, oSV1, oMV1
  };

  return (
    <MyContext.Provider value={contextValue}>
      {children}
    </MyContext.Provider>
  );
};

export const useMyContext = () => {
  const context = useContext(MyContext);
  if (!context) {
    throw new Error('useMyContext must be used within a MyProvider');
  }
  return context;
};
